@import url(https://fonts.googleapis.com/css2?family=Space+Mono:ital,wght@0,400;0,700;1,400;1,700&display=swap);
body {
    font-family: 'Space Mono', monospace;
    color: white;
    background-image: url(/space-game-level-1/static/media/background.afdd00e2.jpg);
    background-size: cover;
}

:root {
    --light-gray: #D9D9D9;
    --dark-gray: #C7C6C4;
    --light-yellow: #F2D785;
    --dark-yellow: #D99152;
    --light-pink: #F2C0A2;
    --dark-pink: #F28F79;
    --light-blue: #3F768C;
    --dark-blue: #223A59;
    --black-blue: #181C26;
    --light-green: #6C7336;
    --dark-green: #142601;
}

#intro {
    text-align: center; 
    height: 90vh;
    -webkit-animation-name: float;
            animation-name: float;
    -webkit-animation-duration: 25s;
            animation-duration: 25s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}

#challenge1 {
    height: 90vh;
    display: none;
    text-align: center;
    -webkit-animation-name: float;
            animation-name: float;
    -webkit-animation-duration: 20s;
            animation-duration: 20s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}

#inspect-p {
    font-size: 1.5rem;
    width: 50rem;
    margin: 2rem auto;
    padding: 1rem;
    border: 2px solid #F2D785;
    border: 2px solid var(--light-yellow);
    border-radius: 8px;
    background-color: rgba(24, 28, 38, 0.6);
}

#inspect-label {
    margin-right: 1rem;
    font-size: 1.3rem;
}

#inspect-input {
    margin-right: 1rem;
}

#challenge2 {
    height: 90vh;
    display: none;
    text-align: center;
    -webkit-animation-name: float;
            animation-name: float;
    -webkit-animation-duration: 20s;
            animation-duration: 20s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}

.password-p {
    font-size: 1.3rem;
    width: 50rem;
    margin: 2rem auto;
    padding: 1rem;
    border: 2px solid #F2D785;
    border: 2px solid var(--light-yellow);
    border-radius: 8px;
    background-color: rgba(24, 28, 38, 0.6);
}

.password-sub {
    font-size: 1.1rem;
    background-color: #181C26;
    background-color: var(--black-blue);
    width: 45rem;
    margin: 1rem auto;
    color: #F2D785;
    color: var(--light-yellow);
}

#challenge3 {
    height: 90vh;
    display: none;
    text-align: center;
    -webkit-animation-name: float;
            animation-name: float;
    -webkit-animation-duration: 20s;
            animation-duration: 20s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}

.button {
    color: #181C26;
    color: var(--black-blue);
    display: block;
    margin: 2rem auto;
    font-family: 'Space Mono', monospace;
    font-weight: 700;
    font-size: 1rem;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 3rem;
    border-radius: 8px;
    background-color: #D9D9D9;
    background-color: var(--light-gray);
    border: 2px solid #223A59;
    border: 2px solid var(--dark-blue);
}

.button:hover {
    background-color: #C7C6C4;
    background-color: var(--dark-gray);
}

#back-button {
    font-family: 'Space Mono', monospace;
    font-size: 1.1rem;
    font-weight: bold;
    background: none;
    border: none;
    color: #D99152;
    color: var(--dark-yellow);
}

@-webkit-keyframes float {
    0% { -webkit-transform: translate(0,  0px); transform: translate(0,  0px); }
    50%  { -webkit-transform: translate(0, 100px); transform: translate(0, 100px); }
    100%   { -webkit-transform: translate(0, -0px); transform: translate(0, -0px); }    
}

@keyframes float {
    0% { -webkit-transform: translate(0,  0px); transform: translate(0,  0px); }
    50%  { -webkit-transform: translate(0, 100px); transform: translate(0, 100px); }
    100%   { -webkit-transform: translate(0, -0px); transform: translate(0, -0px); }    
}
#title {
    background-color: var(--light-yellow);
    color: var(--black-blue);
    width: 23rem;
    margin: 5rem auto 3rem auto;
    border-radius: 4px;
    padding: 1rem;
    border: 2px solid var(--dark-blue);
}

#intro-description {
    font-size: 1.5rem;
    padding: 1rem;
    width: 50rem;
    margin: 0 auto 3rem auto;
    border: 2px solid var(--light-yellow);
    border-radius: 8px;
    background-color: rgba(24, 28, 38, 0.6);
}
.card  {
    display: inline;
    width: 8rem;
    height: 8rem;
    margin: 0.9rem;
    border-radius: 8px;
    font-family: 'Space Mono', monospace;
    border: 2px solid var(--dark-blue);
}

.flipped {
    font-size: 1rem;
    background: tomato;
}

.cardPaired {
    display: none;
}
#keyboard-btn {
    display: inline;
    width: 4rem;
    height: 4rem;
    margin: 0.3rem;
    border-radius: 8px;
    font-family: 'Space Mono', monospace;
    font-size: 1.8rem;
    border: 2px solid var(--dark-blue);
}

#keyboard-btn:hover {
    background-color: var(--dark-gray);
}
